.ng-enter.fade{
  -webkit-animation: fadeIn 0.2s ease;
  animation: fadeIn 0.2s ease;
}
.ng-leave.fade{
  -webkit-animation: fadeOut 0.2s ease;
  animation: fadeOut 0.2s ease;
}
@keyframes fadeIn {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}
@keyframes fadeOut {
  from{
    opacity: 1;
  }
  to{
    opacity: 0;
  }
}
@keyframes showWindow {
  0% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
